@import "../../../../assets/styles/reset.scss";
@import "../../../../assets/styles/com.scss";
@media screen and (min-width: 540px) {  
    .main {
        width: 1200px;
        margin: 0 auto;
        padding-bottom: 172px;
        .tabBar {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 50px;
            .tab {
                & span {
                    font-size: 24px;
                    font-weight: 600;
                    color: rgba(60, 60, 67, 0.6);
                    padding-right: 45px;
                    cursor: pointer;
                }   
                & span:hover, & span.select {
                    color: #000;
                }
            }
        }
        .cardBar {  
            // display: flex;
            // align-items: center;
            height: 294px;
            .noData {
                min-width: 1200px;
                text-align: center;
                min-height: 300px;
                line-height: 300px;
            }
        }
        .between {
            display: flex;
            justify-content: space-between;
        }
        .df {
            display: flex;
        }
        .item {
            display: none;
        }
    }
}


@media screen and (max-width: 540px) { 
    .main { 
        margin: rem(30) 0 rem(8) rem(16);
        .tabBar {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: rem(20);
            .tab {
                & span {
                    font-size: rem(14);
                    font-weight: 600;
                    color: rgba(60, 60, 67, 0.6);
                    padding-right: rem(15);
                    cursor: pointer;
                }   
                & span:hover, & span.select {
                    color: #000;
                }
            }
        }
        .cardBar {
            display: flex;
            align-items: center;
            min-height: rem(150);
            .noData {
                width: rem(343);
                text-align: center;
            }
        }
        .between {
            display: flex;
            justify-content: space-between;
        }
        .df {
            display: flex;
        }
    }
}

