@import "../../assets/styles/reset.scss";
@import "../../assets/styles/com.scss";
@media screen and (min-width: 540px) {  
    .card {
        width: 380px;
        height: 294px;
        background: #FFFFFF;
        border-radius: 24px;
        padding: 20px;
        cursor: pointer;
        .cardTop {
            .content {
                display: flex;
                justify-content: space-between;
            }
            .titleBar {
                display: flex;
                align-items: center;
                .proPic {
                    width: 50px;
                    height: 50px;
                    margin-right: 10px;
                    background: #EFEFEF;
                    border-radius: 50%;
                    overflow: hidden;
                    & img {
                        width: 50px;
                        height: 50px;
                    }
                }
                .title {
                    width: 150px;
                    font-size: 22px;
                    font-weight: bold;
                    color: #000000;
                    margin-bottom: 4px;
                    // display: flex;
                    align-items: center;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    white-space: nowrap;
                    & img {
                        margin-left: 5px;
                        width: 21px;
                        height: 20px;
                    }
                }
                .coin {
                    position: relative;
                    padding: 5px 20px;
                    border-radius: 19px;
                    line-height: 28px;
                    & span {
                        position: absolute;
                        top: 50%;
                        left: 12px;
                        transform: translateY(-50%);
                        width: 4px;
                        height: 4px;
                        border-radius: 50%;
                    }
                }
            }
            .coinPic {
                text-align: right;
                & img {
                    width: 51px;
                    height: 32px;
                    cursor: pointer;
                }
            }
            .time {
                font-size: 12px;
                font-weight: 400;
                color: #000000;
                padding: 5px 0;
                text-align: right;
            }
            .status {
                font-size: 12px;
                text-align: center;
                padding: 6px 18px;
                border-radius: 19px;
                
            }
            .status1 {
                color: #006CED;
                background: rgba(111, 212, 255, 0.2);
            }
            .status2 {
                color: #009A7F;
                background: rgba(0, 221, 183, 0.2);
            }
            .status3 {
                color: #5400C1;
                background: rgba(86, 0, 221, 0.2);
            }
            .status4 {
                color: #E02020;
                background: rgba(255, 46, 46, 0.2);
            }
        }
        .topic {
            position: relative;
            top: -2px;
            font-size: 12px;
            font-weight: 500;
            color: #F6454F;
            line-height: 15px;
            img {
                width: 16px;
                height: 16px;
                margin-right: 5px;
            }
        }
        .cardMid {
            margin-top: 20px;
            .desc {
                display: flex;
                justify-content: space-between;
                & span {
                    font-size: 12px;
                    color: #000000;
                    font-weight: 600;
                }
                & i {
                    font-style: normal;
                    color: rgba(60, 60, 67, 0.6);
                }
            }
            .progressBar {
                margin: 7px 0;
                width: 340px;
                height: 10px;
                background: #EFEFEF;
                border-radius: 7px;
            }
            .full {
                width: 240px;
                height: 10px;
                border-radius: 7px;
            }
        }
        .cardFot {
            display: flex;
            justify-content: space-between;
            margin-top: 20px;
            .desc {
                & p:nth-child(1) {
                    font-size: 12px;
                    color: #000000;
                }
                & p:nth-child(2) { 
                    font-weight: 600;
                    color: #E5A800;
                    font-size: 16px;
                }
                & p:nth-child(3) { 
                    font-weight: 400;
                    color: rgba(60, 60, 67, 0.6);
                    font-size: 12px;
                }
                & img {
                    width: 15px;
                    height: 14px;
                    cursor: pointer;
                }
            }
        }
    }


    .BSC:hover {
        box-shadow: 0px 10px 30px 0px rgba(255, 174, 0, 0.3);
        border: 1px solid #E5A800;
    }

    .HECO:hover {
        box-shadow: 0px 10px 30px 0px rgba(9, 56, 149, 0.1);
        border: 1px solid #1B89FF;
    }
}


@media screen and (max-width: 540px) { 
    .card { 
        width: rem(343);
        height: rem(258);
        background: #FFFFFF;
        border-radius: rem(24);
        padding: rem(16);
        margin-bottom: rem(16);
        .cardTop {
            .content {
                display: flex;
                justify-content: space-between;
            }
            .titleBar {
                display: flex;
                align-items: center;
                .proPic {
                    width: rem(40);
                    height: rem(40);
                    margin-right: rem(10);
                    background: #EFEFEF;
                    border-radius: 50%;
                }
                .title {
                    width: rem(143);
                    display: -webkit-box;
                    word-break: break-all;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: pre-line;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    font-size: rem(20);
                    font-weight: bold;
                    color: #000000;
                    margin-bottom: rem(4);
                    // display: flex;
                    align-items: center;
                    & img {
                        margin-left: 5px;
                        width: rem(16);
                        height: rem(16)
                    }
                }
                .coin {
                    position: relative;
                    padding: rem(4) rem(19);
                    border-radius: rem(19);
                    line-height: rem(23);
                    & span {
                        position: absolute;
                        top: 50%;
                        left: rem(12);
                        transform: translateY(-50%);
                        width: rem(4);
                        height: rem(4);
                        border-radius: 50%;
                    }
                }
            }
            .coinPic {
                text-align: right;
                & img {
                    width: rem(32);
                    height: rem(20);
                    cursor: pointer;
                }
            }
            .time {
                font-size: rem(12);
                font-weight: 400;
                color: #000000;
                padding: rem(6) 0;
                text-align: right;
            }
            .status {
                font-size: 12px;
                text-align: center;
                padding: 6px 18px;
                border-radius: 19px;
                
            }
            .status1 {
                color: #006CED;
                background: rgba(111, 212, 255, 0.2);
            }
            .status2 {
                color: #009A7F;
                background: rgba(0, 221, 183, 0.2);
            }
            .status3 {
                color: #5400C1;
                background: rgba(86, 0, 221, 0.2);
            }
            .status4 {
                color: #E02020;
                background: rgba(255, 46, 46, 0.2);
            }
        }
        .topic {
            position: relative;
            top: -2px;
            font-size: 12px;
            font-weight: 500;
            color: #F6454F;
            line-height: 15px;
            img {
                width: 16px;
                height: 16px;
                margin-right: 5px;
            }
        }
        .cardMid {
            margin-top: rem(15);
            .desc {
                display: flex;
                justify-content: space-between;
                & span {
                    font-size: rem(12);
                    color: #000000;
                    font-weight: 600;
                }
                & i {
                    font-style: normal;
                    color: rgba(60, 60, 67, 0.6);
                }
            }
            .progressBar {
                margin: rem(7) 0;
                width: rem(311);
                height: rem(10);
                background: #EFEFEF;
                border-radius: rem(7);
                overflow: hidden;
            }
            .full {
                width: rem(224);
                height: rem(10);
                border-radius: rem(7);
            }
        }
        .cardFot {
            display: flex;
            justify-content: space-between;
            margin-top: rem(15);
            .desc {
                max-width: rem(150);
                overflow: hidden; 
                text-overflow: ellipsis;
                & p:nth-child(1) {
                    font-size: rem(12);
                    color: #000000;
                }
                & p:nth-child(2) { 
                    white-space: nowrap;
                    font-weight: 600;
                    color: #E5A800;
                    font-size: rem(14);
                }
                & p:nth-child(3) { 
                    font-weight: 400;
                    color: rgba(60, 60, 67, 0.6);
                    font-size: rem(12);
                }
                & img {
                    width: rem(14);
                    height: rem(14);
                    cursor: pointer;
                }
            }
        }
    }
}

.BSC {
    border: 1px solid #FFE85F;
    .cardTop {
        .coin {
            background: rgba(255, 210, 0, 0.2);
            color: $bsc_color;
            & span {
                background: $bsc_color;
            }
        }
    }
    .cardMid {
        .desc {
            & span b{
                color: $bsc_color;
            }
        }
        .full {
            background: $bsc_color;
        }
    }
    .cardFot {
        .desc p:nth-child(2) { 
            color: $bsc_color;
        }
    }
}

.HECO {
    border: 1px solid #01E3F7;
    .cardTop {
        .coin {
            background: rgba(111, 212, 255, 0.2);
            color: $heco_color;
            & span {
                background: $heco_color;
            }
        }
    }
    .cardMid {
        .desc {
            & span b{
                color: $heco_color;
            }
        }
        .full {
            background: $heco_color;
        }
    }
    .cardFot {
        .desc p:nth-child(2) { 
            color: $heco_color;
        }
    }
}